:root  {
	--background-primary: rgb(255, 255, 255);
	--background-secondary: rgb(201, 201, 201);
	--background-tertiary:#F2F2F2;
	--font-primary:rgb(19, 19, 19);
	--font-secondary: rgb(85, 85, 85);
	--color-one: #9E2AD0;
	/* --color-two: #1EC0A2; */
	/* --headings-font: 'Roboto Mono', monospace; */
	--paragraph-font: 'Poppins', sans-serif;
	--heading-font: 'Poppins', sans-serif;
}

[data-theme="dark"] {
	--background-primary: rgb(19, 19, 54);
	--background-secondary:#000000;
	--background-tertiary: #000925;
	/* --background-tertiary: #00134B; */
	--font-primary: #E1E8FF;
	--font-secondary: #C5D4FF;
	--color-one: #2382CF;
	/* --color-two: #1EC0A2; */
	/* --color-two: rgb(255, 166, 0); */
}